/* Reset Select */
select {
	-webkit-appearance: none;
	-moz-appearance: none;
	-ms-appearance: none;
	appearance: none;
	outline: 0;
	box-shadow: none;
	border: 0 !important;
	background: red;
	background-image: none;
}
/* Remove IE arrow */
select::-ms-expand {
	display: none;
}
/* Custom Select */
.select {
	position: relative;
	display: flex;
	width: 100px;
	height: 88px;
	line-height: 3;
	background: red;
	overflow: hidden;
	border-radius: .25em;
	position: absolute;
	top: 0;
	right: 0;
}
@-moz-document url-prefix() {
	.select {
		height: 83px;
	}
}
select {
	flex: 1 1;
	padding: 0 .5em;
	color: #fff;
	cursor: pointer;
	font-size: 1em;
}
/* Arrow */
.select::after {
	content: '\25BC';
	position: absolute;
	top: 0;
	right: 0;
	padding: 0 0em;
	background: rgb(255, 0, 0);
	color: #fff;
	cursor: pointer;
	pointer-events: none;
	transition: .25s all ease;
}
/* Transition */
.select:hover::after {
	color: #f39c12;
}

