.inst-cover {
	padding: 1em;
	background: #000;
	/* animation: pulse 2s infinite; */
	transform: translate(0);
	opacity: 1;
	transition: all 500ms;
}
.inst-cover.left {
	opacity: 0;
	transform: translateX(-200px);
}
.inst-cover.up {
	opacity: 0;
	transform: translateY(200px);
}
.inst-cover.right {
	opacity: 0;
	transform: translateX(200px);
}
.inst-cover:hover {
	-webkit-animation: none;
	        animation: none;
}
@-webkit-keyframes pulse {
	25% {
		transform: scale(99%);
	}
	50% {
		transform: scale(100%);
	}
	100% {
		transform: scale(99%);
	}
}
@keyframes pulse {
	25% {
		transform: scale(99%);
	}
	50% {
		transform: scale(100%);
