	box-sizing: border-box;
	padding: 0;
	margin: 0;
}
html {
	scroll-behavior: smooth;
}
/* width */
::-webkit-scrollbar {
	width: 10px;
	background: #000;
}

/* Track */
::-webkit-scrollbar-track {
	/* box-shadow: inset 0 0 5px grey; */
	border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: red;
	border-radius: 50%;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: rgba(255, 0, 0, 0.288);
}

